        <div class="hot-goods-item" v-for="(item,index) in goodsList" :key="index" @touchstart="goGoodsDetail(item.id)">
          <div class="hot-goods-img">
            <img :src="item.img_src" />
          </div>
          <div class="hot-goods-price">
            <div class="sell-price">${{item.sell_price}}</div>
            <div class="market-price">${{item.market_price}}</div>
          </div>
          <div class="hot-goods-title">{{item.title}}</div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
    props:["goodsList"],
    methods:{
        goGoodsDetail(id){
            this.$emit("touchstart",id)
        }
    }
}
